be included in the\n * output CSS.\n * For more information on global stylesheets, visit the documentation:\n * https://ionicframework.com/docs/layout/global-stylesheets\n */\n\n\n/* Core CSS required for Ionic components to work properly */\n\n@import \"~@ionic/angular/css/core.css\";\n\n/* Basic CSS for apps built with Ionic */\n\n@import \"~@ionic/angular/css/normalize.css\";\n@import \"~@ionic/angular/css/structure.css\";\n@import \"~@ionic/angular/css/typography.css\";\n@import \"~@ionic/angular/css/display.css\";\n\n/* Optional CSS utils that can be commented out */\n\n@import \"~@ionic/angular/css/padding.css\";\n@import \"~@ionic/angular/css/float-elements.css\";\n@import \"~@ionic/angular/css/text-alignment.css\";\n@import \"~@ionic/angular/css/text-transformation.css\";\n@import \"~@ionic/angular/css/flex-utils.css\";\n@import url(\"https://fonts.googleapis.com/css2?family=Roboto&display=swap\");\n@import url(\"https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap\");\n@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');\n@font-face {\n    font-family: \"SF\";\n    //font-style: normal;\n    font-weight: 400;\n    src: url(\"./assets/fonts/SF-Pro-Display-Regular.ttf\");\n}\n\n@font-face {\n    font-family: \"SF\";\n    // font-style: bold;\n    font-weight: 600;\n    src: url(\"./assets/fonts/SFProDisplay-Semibold.ttf\");\n}\n\n\n\n  @font-face {\n    font-family:'SF';\n    font-weight: 500;\n    src:url(\"http://www.apple.com/wss/fonts/SF-Pro-Display/v1/sf-pro-display_medium.eot\");\n    /* (C) 2015 Apple Inc. All rights reserved.*/\n  }\n\n\n/*Custom styles for the project Mariposa*/\n\n\n/*INPUT BOX */\n\n.input {\n    border: solid;\n    border-color: #B8B8B8;\n    border-radius: 8px;\n    border-width: 1px;\n    font-size: 16px;\n    line-height: 19px;\n    font-weight: normal;\n    font-style: normal;\n    height: 44px;\n    margin-top: 8px;\n   \n}\n\n\n/*BLUE FILL BUTTON */\n\n.btnblue {\n    outline: none;\n    border-radius: 8px;\n    color: white;\n    position: fixed;\n    left: 24px;\n    bottom: 16px;\n    right: 24px;\n    margin: 0px;\n    font-size: 14px;\n    font-style: normal;\n    line-height: 16px;\n    font-weight: 600;\n    --box-shadow: none;\n    height: 44px;\n}\n\n.btnblue[disabled] {\n    --background: var(--ion-color-mlgrey);\n    opacity: 100%;\n    left: 24px;\n    bottom: 16px;\n    right: 24px;\n    font-size: 14px;\n    font-style: normal;\n    line-height: 16px;\n    font-weight: 600;\n    --box-shadow: none;\n    height: 44px;\n}\n\n\n/*WHITE FILL BUTTON */\n\n.btnwhite {\n    --color: #4137ff;\n    --background: #ffffff;\n    --border-radius: 8px;\n    --border-color: #4137ff;\n    border-radius: 8px;\n    border-style: solid;\n    border-width: 1px;\n    font-size: 14px;\n    font-style: normal;\n    line-height: 16px;\n    font-weight: 600;\n    left: 24px;\n    bottom: 16px;\n    right: 24px;\n    --box-shadow: none;\n    position: absolute;\n    margin: 0px;\n    height: 44px;\n}\n\n\n/*Standardize div padding accross the app */\n\ndiv {\n    // padding-top: 5px;\n    padding-left: 0px;\n    padding-right: 0px;\n}\n\n\n/*Standardize h5 accross the app. Used for bolder tags */\n\nh5 {\n    line-height: 24px;\n    font-size: 20px;\n}\n\n\n/*Standardize h6 padding accross the app . used for text */\n\nh6 {\n    font-size: 14px;\n    color: #1a1a1a;\n    line-height: 17px;\n    font-weight: 500;\n}\n\n\n/*Standardize ion-grid padding accross the app */\n\nion-grid {\n    padding-left: 24px;\n    padding-right: 24px;\n    padding-bottom: 16px;\n    padding-top: 16px;\n    //height: 100%;\n}\n\nion-icon {\n    //color: red;\n    zoom: 0.6;\n}\n\n#tag {\n    font-style: normal;\n    font-weight: 300;\n    font-size: 16px;\n    line-height: 19px;\n    color: #1a1a1a;\n}\n\n#button {\n    height: 76px;\n    width: 100%;\n    background-color: white;\n    left: 0px;\n    bottom: 0px;\n    position: fixed;\n    box-shadow: 0px -2px 8px rgba(65, 55, 255, 0.08);\n    z-index: 2;\n    padding: 0px;\n}\n\nion-col {\n    padding-left: 0px;\n    padding-right: 0px;\n}\n\n.nametag {\n    margin-top: 10px;\n    font-size: 13px;\n    color: #1a1a1a;\n    font-weight: 300;\n    line-height: 19px;\n}\n\n.warning {\n    color: red;\n    font-size: 14px;\n    line-height: 17px;\n    margin-left: 5px;\n}\n\n.warningicon{\n    zoom:0.6;\n    color:red;\n}\n\n.warningitems{\n    height: 34px;\n}\n\nion-toolbar {\n    ion-grid {\n        padding-bottom: 3px;\n    }\n}\n\n#titletag {\n    font-size: 16px;\n    font-weight: bold;\n    line-height: 19px;\n    color: #1a1a1a;\n}\n\n.smalltext {\n    color: #7e7e7e;\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 17px;\n    font-style: normal;\n}\n\n//header toolbar\n.headtoolbar {\n    height: 88px;\n    box-shadow: 0px 2px 8px rgba(65, 55, 255, 0.14);\n    div {\n        padding: 0px;\n        margin-top: 50px;\n        margin-right: 20px;\n    }\n    ion-col {\n        padding: 0px;\n    }\n    ion-buttons {\n        margin-top: 35px;\n    }\n}\n\nhr.solid {\n    border-top: 1px solid #e5e5e5;\n    margin-top: 11px;\n    margin-bottom: 16px;\n}\n\nion-checkbox {\n    background: #ffffff;\n    border-width: 1px !important;\n    border-style: solid !important;\n    border-radius: 4px;\n    border-color: black !important;\n    --height: 24px;\n    --width: 24px;\n    --checkmark-color: #000000;\n    --background-checked: #ffffff;\n    --border-color: #ffffff;\n    --border-color-checked: #ffffff;\n}\n\nion-input.has-focus.sc-ion-input-md-h {\n    border-color: #4137ff;\n}\n\n.popoverstyle {\n    --backdrop-opacity: 0.7;\n    --background: #1a1a1a;\n}\n\n.popover-translucent.sc-ion-popover-ios-h .popover-content.sc-ion-popover-ios {\n   // top: 96px !important;\n    padding-top: 0px;\n}\n\n.popover-arrow.sc-ion-popover-ios {\n   // top: 86px !important;\n}\n\n.popover-translucent.sc-ion-popover-ios-h .popover-arrow.sc-ion-popover-ios::after {\n    backgroun